@import url(https://fonts.googleapis.com/css?family=Wellfleet&display=swap);
html {
  margin: 0;
  height: 100%;
}

body {
  background-color: beige;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-dist {
    padding-top: 30px;
    padding-bottom: 20px;
}

.app {
    min-height: 100vh;
}
p {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 6;
    overflow: hidden;
}

.header-s {
    box-sizing: border-box;
    width: 100%;
}

.logo-img {
    padding-top: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.font-title {
    font-family: 'Wellfreet', cursive;
}
